import React from 'react';
import {
  Container,
  Box,
  Body,
  Title,
  Text,
  Image,
  Icon,
  Button,
  Brand,
  InnerBrand
} from './styles/listview';

export default function Listview({ children }) {
  return (
    <Container>
      {children}
    </Container>
  );
}

function ListviewContainer({ children }) {
  return(
    <Box>
      {children}
    </Box>
  );
}

function ListviewBody({ children }) {
  return(
    <Body>
      {children}
    </Body>
  );
}

function ListviewTitle({ children }) {
  return(
    <Title>
      {children}
    </Title>
  );
}

function ListviewText({ children }) {
  return(
    <Text>
      {children}
    </Text>
  );
}

function ListviewImage({ src, alt }) {
  return(
    <Image
      src={src}
      alt={alt}
    />
  );
}

function ListviewIcon({ src, alt }) {
  return(
    <Icon
      src={src}
      alt={alt}
    />
  );
}

function ListviewButton({ children }) {
  return(
    <Button
      type="button"
    >
      {children}
    </Button>
  );
}

function ListviewBrand({ children }) {
  return(
    <Brand>
      {children}
    </Brand>
  );
}

function ListviewInnerBrand({ children }) {
  return(
    <InnerBrand>
      {children}
    </InnerBrand>
  );
}

Listview.Container = ListviewContainer;
Listview.Body = ListviewBody;
Listview.Title = ListviewTitle;
Listview.Text = ListviewText;
Listview.Image = ListviewImage;
Listview.Icon = ListviewIcon;
Listview.Button = ListviewButton;
Listview.Brand = ListviewBrand;
Listview.InnerBrand = ListviewInnerBrand;